import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { WrapperComponent } from './wrapper.component';

const routes: Routes = [{ path: '', component: WrapperComponent,
children: [
  {path: '', redirectTo:  'banks-menu' , pathMatch: 'full' } ,
  { path: 'banks-menu', loadChildren: () => import('../banks-menu/banks-menu.module').then(m => m.BanksMenuModule) },
  { path: 'banks-list', loadChildren: () => import('../banks-list/banks-list.module').then(m => m.BanksListModule) },
  { path: 'branch-list', loadChildren: () => import('../branch-list/branch-list.module').then(m => m.BranchListModule) },
  { path: 'banks-operations', loadChildren: () => import('../banks-operations/banks-operations.module').then(m => m.BanksOperationsModule) },
  { path: 'calendar', loadChildren: () => import('../calendar/calendar.module').then(m => m.CalendarModule) },
  { path: 'list', loadChildren: () => import('../list/list.module').then(m => m.ListModule) },
]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WrapperRoutingModule { }
